
$navbar-light-color: white;
$navbar-light-hover-color: white;
$navbar-light-disabled-color: rgba(0,0,0,.3);
$navbar-light-active-color: white;

nav {
  background-color: #007bbf !important;
  .logo-text {
    color: $navbar-light-color;
  }
  
}
.navbar-nav {
    .nav-link {
      color: $navbar-light-color !important;
      

      &:hover,
      &:focus {
        color: $navbar-light-hover-color !important;
        //-webkit-text-stroke-width: 0.4px;
      }

      &.disabled {
        color: $navbar-light-disabled-color !important;
      }
    }

    .show > .nav-link,
    .nav-link.active {
      color: $navbar-light-active-color !important;
      //font-weight: 500;
      //border-bottom: 0.2rem solid;
      //padding-bottom: 0px;
    }
  }

  @media screen and (max-width: 576px) {
    .max-width-575 {
      display: none !important;
    }
  }

  .offcanvas-body {
    .nav-link.active {
      // background: #007bbf;
      // border-radius: 5px;
      // border-right: 3px solid;
      // border-color: white;
      
    }
    ul {
      margin-top: 0;
      margin-bottom: 0;
    }
    span.submenu-icon {
      float: right;
      transform: rotateZ(0deg) rotateX(45deg);
      transform-origin: center
      transition(transform .5s);
    }
    span.submenu-icon.rotation {
      float: right;
      transform: rotateZ(180deg) rotateX(45deg);
    }
  }
  .main-menus .nav-link.active,.main-menus .nav-item.active{
    background: #015888;
    border-radius: 5px;
    border-bottom: 3px solid;
    border-color: white;
  }
  .dropdown-menu {
    background: #007bbf;
  }