.App-logo {
  height: 40vmin;
  pointer-events: none;
}

html,
body {
  overflow-x: hidden; /* Prevent scroll on narrow devices */
}

body {
  padding-top: 56px;
  background: #2b4f64;
}

#main-content {
  padding-top: 16px;
}

.product-button {
  width: 50%;
  background: #007bbf;
  border-color: #007bbf;
}

.info-sec {
  padding: 1.5rem;
}
.info-sec h5 {
  font-style: italic;
}
.nav-link {
  padding: 0.1rem 0.5rem !important;
}


.offcanvas {
  width: 70%;
  top: 61px;
  background: #015888;
}

.offcanvas-backdrop {
  top: 61px;
}

.navbar-toggle {
  border: none;
  background: transparent !important;
  position: relative;
  float: right;
  margin-top: 8px;
  margin-right: 15px;
  margin-bottom: 8px;
  background-color: transparent;
  background-image: none;
  border: 1px solid transparent;
  border-radius: 4px;
}
.navbar-toggle:hover {
  background: transparent !important;
}
.navbar-toggle .icon-bar {
  width: 22px;
  transition: all 0.2s;
  background-color: white;
  display: block;
  width: 22px;
  height: 2px;
  border-radius: 1px;
  margin: 4px 0px;
}
.navbar-toggle .top-bar {
  transform: rotate(45deg);
  transform-origin: 10% 10%;
}
.navbar-toggle .middle-bar {
  opacity: 0;
}
.navbar-toggle .bottom-bar {
  transform: rotate(-45deg);
  transform-origin: 10% 90%;
}
.navbar-toggle.collapsed .top-bar {
  transform: rotate(0);
}
.navbar-toggle.collapsed .middle-bar {
  opacity: 1;
}
.navbar-toggle.collapsed .bottom-bar {
  transform: rotate(0);
}